export default {
	name: "user",
	data() {
		return {
			customTheme: '',
			modalName: null,
			authorityDialog: null,
			setting: {
				color: ''
			},
			info: {
				mark: 0,
				money: 0,
				library: 0,
				collections: '',
				amount: ''
			},
			userInfo: {
				nickName: '',
				avatarUrl: '',
				userId: '',
			},
			colorList: [
				{
					color: 'black',
					label: '墨黑'
				},
				{
					color: 'blue',
					label: '靛青（默认）'
				},
				{
					color: 'red',
					label: '魅红'
				},
				{
					color: 'orange',
					label: '鎏金'
				},
				{
					color: 'montana',
					label: '墨蓝'
				},
				{
					color: 'green',
					label: '翠柳'
				},
				{
					color: 'purple',
					label: '惑紫'
				},
				{
					color: 'pink',
					label: '霞彩'
				}
			]
		}
	},
	
	onLoad() {
		this.customTheme = this.$store.getters.customTheme
		console.log(this.$store.getters.customThemes)
		if (this.$store.state.token) {
			this.$http({
				url: '/user/info'
			}).then(res => {
				this.info = res
			})

		}
		this.userInfo = this.$store.state.userInfo;
	},
	watch: {
		"$store.state.customTheme": function(newValue) {
			this.customTheme = newValue
		}
	},
	methods: {
		confirmSetting() {
			uni.setStorageSync('customTheme', this.setting.color)
			this.$store.commit('customTheme', uni.getStorageSync('customTheme'));
		},
		RadioChange(e) {
			this.setting.color = e.detail.value
			console.log(this.setting.color)
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		hideAuthorityDialog(e) {
			this.authorityDialog = null
		},
		signIn() {
			this.authorityDialog = 'DialogModal2'
		},
		cancelSignIn() {
			this.authorityDialog = null
		},
		getUnreadNumber() {
			this.$http({
				url: '/message/unreadNumber',
				alert: 'none',
				loading: 'none'
			}).then(res => {
				this.$set(this.info, 'unreadNumber', res.sum)
				this.$store.commit('unreadNumber', res.sum)
			})
		},
		authorize() {
			uni.login({
				provider: 'weixin',
				success: code => {
					uni.getUserInfo({
						provider: 'weixin',
						success: info => {
							this.$http({
								url: '/user/login',
								method: 'POST',
								data: {
									code: code.code,
									nickname: info.userInfo.nickName,
									avatar_url: info.userInfo.avatarUrl
								},
								loading: 'none'
							}).then(res => {
								this.$store.commit('userInfo', info.userInfo)
								this.$store.commit('token', res.token)
								this.$http({
									url: '/user/identity',
									loading: 'none'
								}).then(res => {
									if (res.username && res.phone) {
										uni.reLaunch({
											url: './user'
										});
									} else {
										uni.reLaunch({
											url: '../component/form'
										});
									}
								})
							})
						}
					});
				}
			})
		}
	}
}
